:root {
  --dashboard: #565656;
  --dashboardTwo: #d7cec7;
  --primary: #76323f;
  --secundary: #c09f80;
}
body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #d7cec7;
  background-color: var(--dashboardTwo);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.navbar,
.cover {
  position: relative;
  width: 100%;
  height: 500px;
  background-image: url(/static/media/cover.e2f0e0fe.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  border-bottom: 2px solid var(--primary);
  box-shadow: 2px 2px 2px 7px 1px var(--dashboard);
  overflow: hidden;
  opacity: 80%;
}
.cover {
  background: var(--primary) !important;
}

.navbar-content {
  margin-top: 20%;
  -webkit-columns: 2;
          columns: 2;
  text-align: center;
  width: 100%;
}

.Glass {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 20%;
  right: 0;
  margin: auto;
  width: 120px;
  height: 130px;
  border: 1px solid #76323fb9;
  border-radius: 20px;
  box-shadow: 3px 3px 7px 1px #76323f5d;
  background-color: rgba(207, 207, 207, 0.185);
  overflow: hidden;
  background-image: url(/static/media/ifLogo.c01307e7.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 110px;
}
.GlassEffect {
  width: 25px;
  height: 130px;
  background: rgba(255, 255, 255, 0.719);
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-name: Reflection;
          animation-name: Reflection;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
          animation-direction: normal;
}

@-webkit-keyframes Reflection {
  from {
    -webkit-transform: skewX(45deg) translateX(-100px);
            transform: skewX(45deg) translateX(-100px);
  }

  to {
    -webkit-transform: skewX(45deg) translateX(230px);
            transform: skewX(45deg) translateX(230px);
  }
}

@keyframes Reflection {
  from {
    -webkit-transform: skewX(45deg) translateX(-100px);
            transform: skewX(45deg) translateX(-100px);
  }

  to {
    -webkit-transform: skewX(45deg) translateX(230px);
            transform: skewX(45deg) translateX(230px);
  }
}

